.btn-fill-out {
  background-color: transparent;
  border: 1px solid #d82a4f;
  color: #fff;
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.btn-fill-out::before,
.btn-fill-out::after {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  background-color: #d82a4f;
  z-index: -1;
  transition: all 0.3s ease-in-out;
  width: 51%;
}

.btn-fill-out::after {
  right: 0;
  left: auto;
}

.btn-fill-out:hover:before,
.btn-fill-out:hover:after {
  width: 0;
}

.btn-fill-out:hover {
  color: #d82a4f !important;
}

.btn-fill-line {
